import styled from 'styled-components';

export const Container = styled.div`
  padding: 0 15px;
  height: 100%;
  flex-grow: 0;
  flex-shink: 0;
  flex-basis: 320px;
  opacity: ${props => props.done ? 0.6 : 1};
  
  & + div{
      border-left: solid 0.5px rgba(0,0,0,0.5);
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 42px;

    h2 {
      font-weight: 500;
      font-size: 16px;
      padding: 0 10px;
    }

    button {
      width: 30px;
      height: 30px;
      border-radius: 12px;
      background: #3b5bfb;
      border-style: none;
      cursor: pointer;
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  ul{
    margin-top: 30px;
    height: 100%;
  }
`;
